import React from 'react';
import './BookCard.css';
import { FaRegStar, FaStar, FaStarHalfAlt } from 'react-icons/fa';
class BookCard extends React.Component {
    render() {
        return (
            <div className="product-card">
                <div className="badge">New</div>
                <div className="product-tumb" style={{ backgroundImage: 'url(' + this.props.image + ')', backgroundSize: 'cover' }}>
                </div>
                <div className="product-details">
                    <span className="product-catagory">{this.props.title}</span>
                    <div className="product-bottom-details">
                        <div className="product-price">
                            {
                                this.props.stars && this.props.stars.map((star, index) => {
                                    if (star == 5) return <FaStar key={index} />;
                                    else if (star >= 2.5 && star < 5) return <FaStarHalfAlt key={index} />;
                                    else return <FaRegStar key={index} />
                                })
                            }
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default BookCard;